/* App.css */

/* Estilo global */
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #8bbcfc;
  color: #333;
}

/* Encabezado principal */
h1 {
  text-align: center;
  color: #d7e8fa;
  margin: 20px 0;
  font-size: 2.5rem;
  font-weight: bold;
}

/* Contenedor general */
div {
  padding: 20px;
  margin: 20px auto;
  max-width: 900px;
  background-color: #394d4e;
  border-radius: 8px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

/* Encabezados secundarios */
h2 {
  color: #6a38b6;
  border-bottom: 2px solid #3498db;
  padding-bottom: 5px;
  font-size: 1.8rem;
  margin-bottom: 20px;
}

/* Párrafos */
p {
  line-height: 1.6;
  margin-bottom: 15px;
  font-size: 1.1rem;
}

/* Estilo para los textos destacados */
strong {
  font-weight: bold;
  color: #c6ddf5;
}

/* Contenedor de imágenes */
div img {
  background-color: #2c3650;
  border-radius: 8px;
  box-shadow: 0px 2px 4px rgba(17, 36, 99, 0.1);
  transition: transform 0.3s ease;
  max-width: 100%;
  height: auto;
}

div img:hover {
  transform: scale(1.05);
  cursor: pointer;
}

/* Contenedor de videos */
iframe {
  margin: 20px 0;
  border-radius: 8px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.2);
  width: 100%;
  height: 315px;
  border: none;
}

/* Estilo para la galería de imágenes (flexbox) */
div img {
  background-color: #2c3650;
  margin-bottom: 15px;
}

/* Galería de imágenes con flexbox */
div {
  display: flex;
  gap: 15px;
  flex-wrap: wrap;
  justify-content: center;
}

div img {
  max-width: 300px;
  height: auto;
  transition: transform 0.3s ease;
}

div img:hover {
  transform: scale(1.05);
}

/* Ajuste para los videos en pantallas pequeñas */
@media (max-width: 768px) {
  iframe {
    height: 250px;
  }

  div {
    flex-direction: column;
    align-items: center;
  }

  div img {
    max-width: 90%;
  }
}
